.feedback-form {
  max-width: 480px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, .12);
  border-radius: 4px;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  padding: 8px 12px;
}

input, textarea {
  font: inherit;
  padding: 8px;
}

textarea {
  resize: none;
}

.link {
  width: fit-content;
  color: #3030c1;
  background-color: #97a5ff;
  border-radius: 120px 100px 15px;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: 400;
  transition: background-color .3s ease-in-out;
  box-shadow: 0 2px 4px 2px rgba(143, 209, 233, .9);
}

.link:hover {
  background: #82d8e5 linear-gradient(#0147a6, #fff49f);
  transition: background-color 2s linear;
  box-shadow: 4px 4px 0 1px rgba(56, 100, 105, .5);
}

